<template>
  <div class="ticker" :style="{ backgroundImage: 'url(' + require('@/assets/bg.png') + ')' }">
    <div class="title-container">
      <img class="title-pic" src="../assets/title.png" alt />
    </div>

    <transition-group name="fade" tag="span" v-if="msgsToShow.length">
      <div  v-for="(msg) in msgsToShow" class="msgs" v-bind:key="msg.name">
        <div
          class="msg-container flex column"
          :style="{ backgroundImage: 'url(' + require('@/assets/post_bg.png') + ')' }"
        >
          <div class="flex justify-cont-end">
            <div class="flex column box-margin">
              <div class="name-cont">
                <h3>{{msg.name}}</h3>
              </div>
              <div class="date-cont">
                <h5>{{msg.date}}</h5>
              </div>
            </div>
            <div class="msg-pic-cont">
              <img class="msg-pic" :src="msg.profilePic" alt />
            </div>
          </div>

          <div class="msg-cont">
            <h4>{{msg.text}}</h4>
          </div>
        </div>
      </div>
    </transition-group>
  </div>
</template>

<script>
import { msgService } from "../services/msg.service";
export default {
  data() {
    return {
      msgNum: 0,
      msgs: [],
    };
  },
  created() {
    this.startInterval();
  },

  methods: {
    getMsg(msgNum) {
      var currMsg = msgService.getNextMsg(msgNum);
      this.msgNum++;
      this.msgs.unshift(currMsg);
    },
    startInterval() {
      setInterval(() => {
        if (this.msgs.length == 10) {
          this.msgs = [];
          this.msgNum = 0
        } else {
          this.getMsg(this.msgNum);
        }
      }, 3000);
    },
  },
  computed: {
    msgsToShow() {
      
      if (this.msgs.length < 3) {
        return this.msgs;
      } else {
        var newMsgsArray = [];
        for (let i = 0; i < 3; i++) {
          newMsgsArray.push(this.msgs[i]);
        }
        return newMsgsArray;
      }
    },
  },
};
</script>

